<template>
  <transition>
    <div id="qa" v-show="show">
      <div class="resource-loading" v-if="loading">
        <IconLoading></IconLoading>
        <div class="fs4">{{ $t('loadingResource') }}</div>
        <iButton @click="handleClose"
                size="small"
                type="outline">
          {{ $t('close') }}
        </iButton>
      </div>
      <template v-else>
        <iButton @click="handleClose"
                :emit-seconds="20"
                size="small"
                type="outline"
                class="close-button">
          {{ $t('close') }}
        </iButton>
        <img v-if="payload" class="display-resource" :src="`${baseUrl}${payload.img_url}`">
        <span v-else class="fs4">{{ $t('emptyResource') }}</span>
      </template>
    </div>
  </transition>
</template>

<script>
import pong from '@/ue4/pong'
import pongTypes from '@/ue4/pong/types'
import iButton from '@/components/Button'
import IconLoading from '@/components/IconLoading'
import axios from 'axios'

export default {
  components: {
    iButton,
    IconLoading
  },
  data () {
    return {
      show: false,
      nlp: null,
      baseUrl: process.env.NODE_ENV === 'production' ? '/cms' : '',
      payload: null,
      loading: false
    }
  },
  methods: {
    handleNLP (data) {
      console.log('nlp: ', data)
      this.nlp = data
      if (this.nlp.answer && this.nlp.answer.length) {
        const res = this.nlp.answer[0].tts.find(e => e.payload && e.payload !== '{}')
        if (res) {
          this.show = true
          const data = JSON.parse(res.payload)
          this.getResource(data.media_api)
        }
      }
    },
    getResource (url) {
      this.loading = true
      axios
        .get(`${this.baseUrl}${url}`)
        .then(({ status, data }) => {
          if (status === 200 && data) {
            this.payload = data
          }
        })
        .catch((err) => {
          console.log(': ', err)
          this.payload = null
        })
        .finally(() => {
          this.loading = false
