          //   // img_url: require('@/assets/images/icon-loading.png')
          // }
        })
    },
    handleClose () {
      this.show = false
    }
  },
  created () {
    window.sendCmd = (action, data) => {
      if (action === 'nlp') {
        this.handleNLP(data)
      }
    }
